import React from 'react';
import s from './About.module.css';
import fhoto from 'url:./img/big-photo.png';

function About() {
    return(
        <section className={s.about}>
            <div className={s.photoWrapper}>
                <img src={fhoto} alt="" />
            </div>
            <div className={s.aboutWrapper}>
                <h2 className={s.nameSection}> </h2>
                <div className={s.textWrapper}>
                    <p className={s.text}>  .    ,
                      ,     ,
                         -.</p>
                    <p className={s.text}>   (  ),     ,
                            ,      , a&nbsp;&nbsp;&nbsp;   !</p>
                    <p className={s.text}>    </p>
                    <p className={s.text}>    
                                    </p>
                    <p className={s.text}>    
                                    </p>
                    <p className={s.text}>    
                                    </p>
                    <p className={s.text}>    
                                    </p>
                    <p className={s.text}>    
                                    </p>
                    <p className={s.text}>    
                                    </p>
                </div>
                <a className={`${s.linkDownload} ${s.linkDownload_position}`}> </a>
            </div>    
        </section>
    );
}

export default About;